






























import { Component, Vue } from "vue-property-decorator";
import Axios from "axios";

interface State {
  value: string;
  timestamp: Date;
}

interface Response {
  current: State;
  history: State[];
}

@Component
export default class AnyoneThere extends Vue {
  state: Response = {
    current: { value: "UNKNOWN", timestamp: new Date() },
    history: [],
  };
  interval: number | undefined;
  update(): void {
    Axios.get("https://amiworking.shapingideas.fyi").then((r) => {
      this.state = r.data;
    });
  }

  mounted(): void {
    this.update();
    this.interval = setInterval(this.update, 500);
  }

  destroyed(): void {
    clearInterval(this.interval);
  }

  getStateClass(state: string): string {
    switch (state) {
      case "PRESENT":
        return "state-present";
      case "NOT_PRESENT":
        return "state-not-present";
      default:
        return "";
    }
  }

  prettyPrintDate(d: string): string {
    return Date.parse(d).toLocaleString();
  }
}
